import notify from "common/notify";

console.log('rest');

var $modal = $('#member-modal-form').parents('.modal');
let $form = $('#member-modal-form');
let validator = $form.validate({
    rules: {
        'deadline': {
            required: true,
            date: true
        },
        levelId: {
            required: true,
        }
    },
    messages: {
        'deadline': {
            required: '请输入会员截止日期!',
            date: '请输入日期!'
        },
        levelId: {
            required: '请选择会员等级!'
        }
    }
});   

$("#deadline").datetimepicker({
    language: 'zh-CN',
    autoclose: true,
    format: 'yyyy-mm-dd',
    minView: 'month'
})

$('#add-save-btn').click(function() {
  if(validator.form()) {
    $.post($form.attr('action'), $form.serialize(), function(html) {
      notify('success','新会员添加成功');
      window.location.reload();
    }).error(function(){
      notify('danger','新会员添加失败');
    });
  }
})